import {
  Component,
  OnInit,
  EventEmitter,
  Output,
  ChangeDetectorRef,
  ViewChild,
  AfterViewInit,
  Input,
  HostListener,
  ChangeDetectionStrategy,
  ViewChildren,
  QueryList,
  ElementRef,
} from '@angular/core';

import { ShortcutInput, AllowIn } from 'ng-keyboard-shortcuts';
import { TranslateModule } from '@ngx-translate/core';
import {
  CheckBoxType,
  ConfirmButtonConfig,
  DynamicButton,
  GeniusSummaryGroup,
  GroupMessage,
  KindDialog,
  RadioBoxType,
} from './confirm-dialog.model';
import {
  DxButtonComponent,
  DxButtonModule,
  DxCheckBoxModule,
  DxPopupModule,
  DxRadioGroupModule,
} from 'devextreme-angular';
import { CommonModule } from '@angular/common';
import { ReplacePipe } from './confirm-dialog-pipe';
import { KeyboardShortcutsModule } from 'ng-keyboard-shortcuts';
import { EventService } from '../event.service';

@Component({
  selector: 'app-confirm-dialog',
  templateUrl: './confirm-dialog.component.html',
  styleUrls: ['./confirm-dialog.component.scss'],
  standalone: true,
  imports: [
    CommonModule,
    DxPopupModule,
    DxButtonModule,
    DxRadioGroupModule,
    DxCheckBoxModule,
    ReplacePipe,
    KeyboardShortcutsModule,
    TranslateModule,
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ConfirmDialogComponent implements OnInit, AfterViewInit {
  @Output() onRemoving = new EventEmitter<
    | true
    | CheckBoxType[]
    | RadioBoxType[]
    | GroupMessage
    | { checkBoxList: CheckBoxType[]; radioBoxList: RadioBoxType[] }
  >();
  @Output() onClosing = new EventEmitter();
  @Output() onCanceling = new EventEmitter();
  @Output() onDynamicBtnClicked = new EventEmitter<any>();
  @Output() onDestroy = new EventEmitter();
  @ViewChild('nobtn') nobtn: DxButtonComponent = {} as DxButtonComponent;
  @ViewChild('yesbtn') yesbtn: DxButtonComponent = {} as DxButtonComponent;
  @ViewChild('cancelbtn') cancelbtn: DxButtonComponent =
    {} as DxButtonComponent;
  @ViewChildren('dynamicButtons') dynamicButtons: QueryList<ElementRef> =
    {} as QueryList<ElementRef>;

  group: any = null;
  @Input() btnConfig: ConfirmButtonConfig = 'yesno';
  @Input() checkBoxList: CheckBoxType[] = []; // lista checkboxów
  @Input() confirmHeader: string = '';
  @Input() confirmText: string = 'confirmDelete';
  @Input() disableRefreshShortcut = false;
  @Input() dynamicBtns: DynamicButton[] | any = null;
  @Input() genius: GeniusSummaryGroup[] | any = null;
  @Input() groupMsg: GroupMessage | any = null;
  @Input() isVisible = false;
  @Input() kindDialog: KindDialog = 'question';
  @Input() paramsMsg: { [key: string]: string | number } = {};
  @Input() radioBoxList: RadioBoxType[] = []; // lista radiobuttonow
  @Input() showIcon: boolean = true;
  @Input() textAligne: string = '';
  @Input() title: string = '';
  @Input() width: number | string = 420;
  shortcuts: ShortcutInput[] = [];
  unicalGuid = new Date().getTime() + Math.round(Math.random() * 10000);

  constructor(public event: EventService, public cd: ChangeDetectorRef) {}

  ngOnInit(): void {
    if (!this.width) this.width = 420;
    if (!this.title) this.title = 'information';

    if (this.kindDialog == 'question') this.title = 'nests.types.question';
    if (this.kindDialog == 'error') this.title = 'error';
    if (this.kindDialog == 'warning') this.title = 'warning';
  }

  onCancel = () => {
    setTimeout(() => {
      try {
        this.isVisible = false;
        this.onCanceling.emit(true);
      } catch {}
    }, 0);
  };

  setFocus() {
    setTimeout(() => {
      if (this.cancelbtn) {
        this.event.setFocus(this.cancelbtn);
      } else {
        this.event.setFocus(this.nobtn);
      }
    }, 300);
  }

  onInit(e: any) {
    e.component.registerKeyHandler('escape', function () {});
  }

  ngAfterViewInit() {
    this.shortcuts.push({
      key: 'escape',
      allowIn: [AllowIn.Input, AllowIn.Select, AllowIn.Textarea],
      command: () => {
        this.btnConfig == 'yesnocancel' ? this.onCancel() : this.onClose();
      },
      preventDefault: true,
    });
  }

  onClose = (e?) => {
    setTimeout(() => {
      try {
        (this.nobtn as any).element.nativeElement.blur();
        this.isVisible = false;
        this.onClosing.emit(e || true);
      } catch {}
    }, 0);
  };

  ngModelChange() {
    if (!this.isVisible) this.cd.detectChanges();
    if (this.isVisible && this.dynamicBtns) {
      setTimeout(() => {
        this.dynamicButtons?.last?.nativeElement?.firstElementChild?.focus();
      }, 600);
    }
  }

  ngOnDestroy() {
    this.onDestroy.emit(true);
    this.event.onHiddenPopUp(this.unicalGuid);
  }

  onRemove = () => {
    let ret:
      | undefined
      | true
      | CheckBoxType[]
      | RadioBoxType[]
      | GroupMessage
      | { checkBoxList: CheckBoxType[]; radioBoxList: RadioBoxType[] };
    if (!this.checkBoxList && !this.radioBoxList) ret = true;
    if (this.checkBoxList && !this.radioBoxList) ret = this.checkBoxList;
    if (!this.checkBoxList && this.radioBoxList) ret = this.radioBoxList;
    if (this.checkBoxList && this.radioBoxList)
      ret = {
        checkBoxList: this.checkBoxList,
        radioBoxList: this.radioBoxList,
      };
    if (this.groupMsg) ret = this.groupMsg;

    this.isVisible = false;
    this.onRemoving.emit(ret);
  };

  onDynamicBtnClick(e) {
    switch (e.action) {
      case 'defaultConfirm':
        this.onRemove();
        break;
      case 'defaultClose':
        this.onClose();
        break;
      case 'defaultCancel':
        this.onCancel();
        break;
      default:
        this.onDynamicBtnClicked.emit(e.action);
        break;
    }
  }

  disabledButton(check) {
    return check.disabled;
  }

  checkValue(check) {
    return check.value;
  }

  onDynamicBtnArrowLeft(i) {
    if (i > 0) {
      let btnToFocus = document.getElementById(
        'btn' + (i - 1) + this.unicalGuid
      );
      if (btnToFocus) {
        btnToFocus.focus();
      }
    }
  }

  onDynamicBtnArrowRight(i) {
    if (i < this.dynamicBtns.length - 1) {
      let btnToFocus = document.getElementById(
        'btn' + (i + 1) + this.unicalGuid
      );
      if (btnToFocus) {
        btnToFocus.focus();
      }
    }
  }
}
